import { Button } from '@mui/material';
import React from 'react';
import { useLoadingImg } from '../../../../hook/LoadingImg';
import IMG from '../../../../assets/imgs/bg.png';
import placeholder from '../../../../assets/imgs/pl.png';
import { scrollFrom } from '../../../../helpers/scroll';

const TitleChapter = ({ className, title, about }) => {
  const loaded = useLoadingImg(IMG);

  return (
    <div
      className={`${className}__hero hero`}
      style={{ backgroundImage: `url(${loaded || placeholder}` }}
    >
      <div className={`${className}__hero-text hero__text`}>
        <h3 className={`${className}__hero-subtitle hero__subtitle`}>
          Курсовой проект
        </h3>
        <h1 className={`${className}__hero-title hero__title`}>{title}</h1>
      </div>
      <div className={`${className}__hero-about hero__about`}>{about}</div>
      <div className={`${className}__hero-button hero__button`}>
        <Button
          onClick={() => {
            scrollFrom();
          }}
          sx={{
            width: {
              desktop: `243px`,
              laptop: `200px`,
              table: `180px`,
              mobile: `150px`,
            },
            height: {
              desktop: `78px`,
              laptop: `70px`,
              table: `65px`,
              mobile: `40px`,
            },
            border: `1px solid white`,
            borderRadius: '192px',
            color: 'white',
            background: `linear-gradient(328.68deg, #98AA28 13.03%, #D6FF7E 120.61%)`,
            boxShadow: `0px 34px 50px rgba(7, 63, 67, 0.07)`,
          }}
        >
          <span className={`${className}__hero-button-text hero__button-text`}>
            Начать
          </span>
        </Button>
      </div>
    </div>
  );
};

export default TitleChapter;
