import { Container } from '@mui/material';
import React from 'react';

const ContainerCustom = ({ children }) => {
  return (
    <Container
      sx={{
        width: {
          desktop: `1140px`,
          laptop: `960px`,
          table: `720px`,
          mobile: `100%`,
        },
      }}
    >
      {children}
    </Container>
  );
};

export default ContainerCustom;
