import React from 'react';
import CenterChapter from '../../common/chapter/centerchapter';
import RightChapter from '../../common/chapter/rightchapter';
import SliderChapter from '../../common/chapter/sliderchapter';
import TitleChapter from '../../common/chapter/titlechapter';

const InfoPage = () => {
  return (
    <div className="info-page">
      <TitleChapter
        className={'info-page'}
        title={'Информационный блог'}
        about={'В данном разделе показана основная информация по данной теме'}
      />
      <RightChapter
        className={'second-chapter'}
        text={[
          'Расчетать периодический платеж в рамках срочного вклада в Excel(Google Table)',
          'Cоздание программы  на JavaScript',
        ]}
        topic={'Цели курсовой:'}
        img={<img src="./img/1.png" />}
        data={'scroll'}
      />
      <CenterChapter
        className={'three-chapter'}
        text={['чето надо', 'ffsdfsdf', 'sdggdsg']}
        topic={'Задачи курсовой:'}
        img={<img src="./img/1.png" />}
      />
      <SliderChapter
        className={'four-chapter'}
        topic={'Актуальность:'}
      />
    </div>
  );
};

export default InfoPage;
