import React from 'react';
import ContainerCustom from '../../container';

const rightChapter = ({ className, img, text = [], topic, data }) => {
  return (
    <div
      className={`${className}__content right`}
      data-to="scroll"
    >
      <ContainerCustom>
        <div className={`${className}__content right___content`}>
          <div className={`${className}__blog-1 right___blog-1`}>
            <h2 className={`${className}__title right___title`}>{topic}</h2>
            <ul className={`${className}__list right___list`}>
              {text?.map((el, index) => (
                <li
                  key={index + 1}
                  className={`${className}__item right___item`}
                >
                  {el}
                </li>
              ))}
            </ul>
          </div>
          <div className={`${className}__blog-2 right___blog-2`}>{img}</div>
        </div>
      </ContainerCustom>
    </div>
  );
};

export default rightChapter;
