import React from 'react';
import { Swiper, SwiperSlide } from 'swiper/react';
import ContainerCustom from '../../container';
import 'swiper/css/lazy';
import 'swiper/css';
import 'swiper/css/zoom';
import { Lazy, Zoom } from 'swiper';
import { swiperSliderContent } from '../../../../helpers/content';

const SliderChapter = ({ className, text, topic }) => {
  return (
    <div className={`${className}__sliderChapter sliderChapter`}>
      <ContainerCustom>
        <h2 className={`${className}__title sliderChapter__title`}>{topic}</h2>
        <div className={`${className}__content sliderChapter__slider-content`}>
          <Swiper
            spaceBetween={10}
            lazy={true}
            loop
            preloadImages={false}
            modules={[Lazy, Zoom]}
            breakpoints={{
              1200: {
                slidesPerView: 3,
              },
              1024: {
                slidesPerView: 2,
              },
              720: {
                slidesPerView: 2,
              },
              0: {
                slidesPerView: 1,
                zoom: true,
              },
            }}
            className="mySwiper"
          >
            {swiperSliderContent.map((el) => (
              <SwiperSlide key={el.id}>
                <div className="slider__img swiper-zoom-container">
                  <img
                    data-src="./img/img-1.jpg"
                    className="swiper-lazy"
                  />
                  <div className="swiper-lazy-preloader swiper-lazy-preloader-black"></div>
                </div>
                <div className="slider__text ">{el.text}</div>
              </SwiperSlide>
            ))}
          </Swiper>
        </div>
      </ContainerCustom>
    </div>
  );
};

export default SliderChapter;
