import React from 'react';
import { contentZadach } from '../../../../helpers/content';
import ContainerCustom from '../../container';

const CenterChapter = ({ className, img, text = [], topic }) => {
  return (
    <div className={`${className}__content center`}>
      <ContainerCustom>
        <div className={`${className}__content center__content`}>
          <div className={`${className}__blog-1 center__blog-1`}>
            <h2 className={`${className}__title center__title`}>{topic}</h2>
          </div>
          <div className={`${className}__list  center__blog-2`}>
            {contentZadach.map((el) => (
              <div
                className={`${className}__item center__item`}
                key={el.id}
              >
                <div>
                  <img
                    src={el.src}
                    alt=""
                  />
                </div>
                <h2>{el.text}</h2>
              </div>
            ))}
          </div>
        </div>
      </ContainerCustom>
    </div>
  );
};

export default CenterChapter;
